@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Home header img{width:100%}.Home h2{margin-bottom:30px}.Home .content-body{padding:40px 20px;background-color:#0076CE;color:#fff;text-align:left}.Home .content-body p{text-align:justify}.Home .content-body a{color:#fff}.Home .content-body a:hover{color:#fff}.Home .content-body .number-container{display:inline-block;height:100%;width:40px}.Home .content-body .number{font-size:1.7em;text-align:center;width:40px;height:40px;background-color:#fff;color:#0076CE;border-radius:50%}.Home .content-body .number span{margin:auto auto;font-weight:900}.Home .content-body .cat-rows{margin:20px 0px}.Home .content-body .cat-rows .col-md-2{padding:0}.Home .content-body .categories{font-size:1.3em}.Home .poster{width:100%;padding:0 50px}

.Dashboards{color:#fff;width:100%;height:100%}.Dashboards .title{text-align:left;background-color:#0C4587}.Dashboards .title h1{padding:20px 0;margin:0}.Dashboards .description{background-color:#0076CE;text-align:left;padding:20px 0px}.Dashboards .description p{font-size:1.5em}.Dashboards .iframe-container{width:100%;height:100%}.Dashboards iframe{width:100%;height:100%}.Dashboards .navigation{color:#012A84}.Dashboards .navigation a{color:#012A84}.Dashboards .navigation ul{margin:10px 0px}.Dashboards .navigation li{border:1px solid #012A84;border-radius:5px;display:inline-block;padding:3px 10px;margin:0 5px}.Dashboards .navigation li:hover{background-color:#012A84;color:#fff;cursor:pointer;border-radius:5px}

html, body, #app {
  width: 100%;
  height: 100%;
  font-family: 'Quicksand', sans-serif;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

